import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import firebaseApp from '../config';
import firebase from 'firebase';

const useStyles = makeStyles(theme => ({
	textField: {
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
	},
	root: {
		display: 'flex',
	},
}));

const db = firebaseApp.firestore();

export default function LinkForm(props) {
	const classes = useStyles();
	const { data } = props;
	const [state, setState] = React.useState({
		semester: '',
		courseId: '',
		year: '',
		url: ''
	});
	
	const handleForm = e => {
		const { courseId, year, url, semester } = state;
		
		let link = {};
		link[`${courseId}.${year}.${semester}`] = firebase.firestore.FieldValue.arrayUnion({'url':url});

		if( courseId.length != 5 ){
			alert('invalid course id')
			return;
		}

		if( year.length != 4 ){
			alert('invalid year')
			return;
		}

		if( ! ['a','b','c'].includes(semester) ){
			alert('invalid semester')
			return;
		}

		if( ! url ){
			alert('invalid url')
			return
		}

		db.collection('link_list').doc('courses').update(link)
			.then(function(docRef) {
				console.log({'success': true});
				window.location.reload(); // TODO: Use state inorder to fix this
			})
			.catch(function(error) {
				alert('error');
				console.log({'success': false, 'error': error});
			});
    };

    const handleChange = name => event => {
        setState({ ...state, [name]: event.target.value });
    };


	return (
	  <form className={classes.container} noValidate autoComplete="off" onSubmit={(e) => { e.preventDefault(); handleForm(e); }}>
		<TextField
			id="add_course_courseId"
			name="courseId"
			onChange={handleChange('courseId')}
			label="Course Number"
			value={state.courseId}
			style={{ margin: '8px 0' }}
			fullWidth
			margin="normal"
			type="number"
			InputLabelProps={{
				shrink: true,
			}}
			inputProps={{
				maxLength: 5
			}}
		/>
		<TextField
			id="add_course_year"
			name="year"
			onChange={handleChange('year')}
			label="Year"
			style={{ margin: '8px 0' }}
			fullWidth
			value={state.year}
			margin="normal"
			type="number"
			InputLabelProps={{
				shrink: true,
			}}
			inputProps={{
				maxLength: 4
			}}
		/>

		<FormControl component="fieldset" style={{ margin: '8px 0' }}>
			<FormLabel component="legend">Semester</FormLabel>
			<FormGroup row style={{textAlign: 'center'}}>
				<FormControlLabel
					control={
						<Checkbox checked={state.semester === 'c'} onChange={handleChange('semester')} value="c" />}
					label="ג"
				/>
				<FormControlLabel
					control={<Checkbox checked={state.semester === 'b'} onChange={handleChange('semester')} value="b" />}
					label="ב"
				/>
				<FormControlLabel
					control={<Checkbox checked={state.semester === 'a'} onChange={handleChange('semester')} value="a" />}
					label="א"
				/>
			</FormGroup>
		</FormControl>
		<TextField
			id="add_course_url"
			name="url"
			onChange={handleChange('url')}
			label="url"
			style={{ margin: '8px 0' }}
			fullWidth
			value={state.url}
			margin="normal"
			InputLabelProps={{
				shrink: true,
			}}
		/>
		<Button fullWidth variant="contained" style={{ marginTop: '8px' }} color="primary" type="submit">
			SAVE
		</Button>
		{ !!data && (
			<Button fullWidth variant="contained" style={{ marginTop: '8px' }} color="secondary">
				DELETE
			</Button>
		) }
		

	  </form>
	)
}