import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import Paper from '@material-ui/core/Paper';

import LinkListRow from './LinkListRow';

const useStyles = makeStyles({
	root: {
		width: '100%',
	},
	table: {
		width: '100%',
	},
});

export default function LinkList(props) {
	const classes = useStyles();
	const { data: course, courseId } = props;

	if ( ! course || course === [] ) {
      return 'loading ...';
	}
	
	let rows = [];
	let index = 0
    
    for( let year in course ){
		for( let semester in course[year] ){
			rows.push(...course[year][semester].map( data => (
				{ year, semester, url: data.url, index }
			 ) ) );
			 index ++;
		}
	}

	return (
	  <Paper className={classes.root}>
		<Table className={classes.table} aria-label="simple table">
		  <TableBody>
			{rows.map((row) => (
				<LinkListRow data={row} courseId={courseId} />
			))}
		  </TableBody>
		</Table>
	  </Paper>
	);
  
}