import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import firebase from 'firebase';
import firebaseApp from '../config.js'

import EditLinkModal from './EditLinkModal';

const useStyles = makeStyles(theme => ({
	url: {
		textTransform: 'initial',
	},
	button: {
		marginTop: 0,
		marginBottom: 0,
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
	},
}));

const db = firebaseApp.firestore();

export default function LinkList(props) {
	const classes = useStyles();
	const [modalState, setModalState] = React.useState(false);
	const { data: row, courseId } = props;

	const removeItem = (row) => {

		let link = {};
		link[`${row.courseId}.${row.year}.${row.semester}`] = firebase.firestore.FieldValue.arrayRemove({'url':row.url});

		db.collection('link_list').doc('courses').update(link)
			.then(function(docRef) {
				console.log({'success': true});
				window.location.reload(); // TODO: Use state inorder to fix this
			})
			.catch(function(error) {
				alert('error');
				console.log({'success': false, 'error': error});
			});
	}

    if ( ! row ) {
      return 'loading ...';
	}
	
	row.courseId = courseId

	return (
		<TableRow >
			<TableCell component="th" scope="row">
				{row.year}
			</TableCell>
			<TableCell>{row.semester}</TableCell>
			<TableCell>
				<Button target="_blank" href={row.url} className={classes.url}>
					{row.url}
				</Button>
			</TableCell>
			<TableCell align="right">
				<Button target="_blank" href={`https://www.openu.ac.il/courses/${courseId}.htm`} color="primary" size="small" className={classes.button}>
					Course Site
				</Button>

				<Button color="secondary" size="small" className={classes.button} onClick={() => { removeItem(row) }}>
					Delete
				</Button>
			</TableCell>
			<EditLinkModal callback={setModalState} open={modalState} data={row} />
		</TableRow>
	);
  
}