import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

import PropTypes from 'prop-types';
import firebaseApp from '../config.js';

import CourseFilter from '../components/CourseFilter';
import CoursesList from '../components/CourseList';
import AddLinkModal from '../components/AddLinkModal';

const styles = theme => ({
	button: {
		margin: `${theme.spacing(1)}px auto ${theme.spacing(2)}px`,
		width: '50%',
		display: 'block'
	},
	fab: {
		position: 'fixed',
		right: theme.spacing(2),
		bottom: theme.spacing(2)
	}
});
  
const db = firebaseApp.firestore();
const docRef = db.collection("link_list").doc('courses');

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			courses: [],
			filteredList: [],
			filter: '',
			modalState: false
		};

		this.filterList = this.filterList.bind(this);
		this.filterCallback = this.filterCallback.bind(this);
		this.modalCallback = this.modalCallback.bind(this);
	}

	componentWillMount(){
		docRef.get().then((doc) => {
			if (doc.exists) {
				this.setState({courses: doc.data(), filteredList: doc.data()})
			} else {
				alert("No such document!");
			}
		}).catch(function(error) {
			alert("Error getting document:", error);
		});
	}

	componentDidUpdate(prevProps, prevState) {
		if (this.state.filter !== prevState.filter) {
			this.filterList();
		}
	}

	filterList() {
		let courses = this.state.courses;
		let filter = this.state.filter;

		if( filter === '' ){
			return
		}

		const filtered = Object.keys(courses)
			.filter(key => key.includes(filter))
			.reduce((obj, key) => {
				obj[key] = courses[key];
				return obj;
			}, {});


		this.setState({ filteredList: filtered })
	}

	filterCallback(text) {
		this.setState({filter: text})
	}

	modalCallback(open){
		this.setState({modalState: open})
	}
	
	render() {
		const { filteredList, modalState } = this.state
		const { classes } = this.props;
		const filterCallback = this.filterCallback;
		const modalCallback = this.modalCallback;

	  	return (
		<React.Fragment>
			<CssBaseline />
			<Container>
				<CourseFilter callback={filterCallback} />
				<CoursesList data={filteredList} />
				<Fab color="primary" aria-label="add" className={classes.fab} onClick={() => { modalCallback(true); }}>
					<AddIcon />
				</Fab>
				<AddLinkModal callback={modalCallback} open={modalState} />
			</Container>
		</React.Fragment>
		);
	}
}

App.propTypes = {
	classes: PropTypes.object.isRequired,
};
  
export default withStyles(styles)(App);